import { Plus, Search, Package, Receipt, Edit, Eye } from 'lucide-react'
import { useMemo } from 'react'
import { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { BusinessInventoryItem, BusinessInventoryStats, Transaction } from '@/types'
import { businessInventoryService, transactionService, projectService } from '@/services/inventoryService'
import { formatCurrency, formatDate } from '@/utils/dateUtils'

interface FilterOptions {
  status?: string
  searchQuery?: string
}

export default function BusinessInventory() {
  const [activeTab, setActiveTab] = useState<'inventory' | 'transactions'>('inventory')
  const [items, setItems] = useState<BusinessInventoryItem[]>([])
  const [transactions, setTransactions] = useState<Transaction[]>([])
  const [stats, setStats] = useState<BusinessInventoryStats | null>(null)
  const [isLoading, setIsLoading] = useState(true)
  const [filters, setFilters] = useState<FilterOptions>({
    status: '',
    searchQuery: ''
  })
  const [transactionStatusFilter, setTransactionStatusFilter] = useState<string>('')

  // Compute filtered transactions
  const filteredTransactions = useMemo(() => {
    if (!transactionStatusFilter) return transactions
    return transactions.filter(t => t.status === transactionStatusFilter)
  }, [transactions, transactionStatusFilter])

  const tabs = [
    { id: 'inventory' as const, name: 'Inventory', icon: Package },
    { id: 'transactions' as const, name: 'Transactions', icon: Receipt }
  ]

  useEffect(() => {
    loadBusinessInventory()
    loadStats()
    loadBusinessTransactions()
  }, [])

  // Subscribe to real-time updates for inventory
  useEffect(() => {
    const unsubscribe = businessInventoryService.subscribeToBusinessInventory(
      (updatedItems) => {
        setItems(updatedItems)
        setIsLoading(false)
      },
      filters
    )

    return unsubscribe
  }, [filters])

  // Subscribe to real-time updates for transactions when on transactions tab
  useEffect(() => {
    if (activeTab !== 'transactions') return

    const loadTransactionsOnTabChange = async () => {
      await loadBusinessTransactions()
    }

    loadTransactionsOnTabChange()
  }, [activeTab])

  const loadBusinessInventory = async () => {
    try {
      const data = await businessInventoryService.getBusinessInventoryItems(filters)
      setItems(data)
    } catch (error) {
      console.error('Error loading business inventory:', error)
      setItems([])
    } finally {
      setIsLoading(false)
    }
  }

  const loadStats = async () => {
    try {
      const statsData = await businessInventoryService.getBusinessInventoryStats()
      setStats(statsData)
    } catch (error) {
      console.error('Error loading stats:', error)
    }
  }

  const loadBusinessTransactions = async () => {
    try {
      // Get all projects first
      const projects = await projectService.getProjects()

      // Collect all transactions from all projects that are inventory-related
      const allTransactions: Transaction[] = []

      for (const project of projects) {
        try {
          const projectTransactions = await transactionService.getTransactions(project.id)
          // Filter for inventory-related transactions (those with reimbursement_type or trigger_event related to inventory)
          const inventoryTransactions = projectTransactions.filter(t =>
            t.reimbursement_type ||
            t.trigger_event === 'Inventory allocation' ||
            t.trigger_event === 'Inventory return' ||
            t.trigger_event === 'Purchase from client' ||
            t.source?.toLowerCase().includes('inventory')
          )
          allTransactions.push(...inventoryTransactions)
        } catch (error) {
          console.error(`Error loading transactions for project ${project.id}:`, error)
        }
      }

      // Sort by creation date, newest first
      allTransactions.sort((a, b) => new Date(b.created_at).getTime() - new Date(a.created_at).getTime())
      setTransactions(allTransactions)
    } catch (error) {
      console.error('Error loading business transactions:', error)
      setTransactions([])
    }
  }

  const handleFilterChange = (newFilters: FilterOptions) => {
    setFilters(newFilters)
    setIsLoading(true)
  }

  const handleSearchChange = (searchQuery: string) => {
    handleFilterChange({ ...filters, searchQuery })
  }

  const handleStatusFilterChange = (status: string) => {
    handleFilterChange({ ...filters, status: status || undefined })
  }

  if (isLoading) {
    return (
      <div className="flex justify-center items-center h-32">
        <div className="animate-spin rounded-full h-6 w-6 border-b-2 border-primary-600"></div>
      </div>
    )
  }

  return (
    <div className="space-y-6">
      {/* Stats Cards - Only show on Inventory tab */}
      {activeTab === 'inventory' && stats && (
        <div className="grid grid-cols-1 gap-5 sm:grid-cols-2 lg:grid-cols-4">
          <div className="bg-white overflow-hidden shadow rounded-lg">
            <div className="p-5">
              <div className="flex items-center">
                <div className="flex-shrink-0">
                  <div className="w-8 h-8 bg-blue-500 rounded-md flex items-center justify-center">
                    <span className="text-white font-semibold text-sm">{stats.totalItems}</span>
                  </div>
                </div>
                <div className="ml-5 w-0 flex-1">
                  <dl>
                    <dt className="text-sm font-medium text-gray-500 truncate">
                      Total Items
                    </dt>
                    <dd className="text-lg font-medium text-gray-900">
                      {stats.totalItems}
                    </dd>
                  </dl>
                </div>
              </div>
            </div>
          </div>

          <div className="bg-white overflow-hidden shadow rounded-lg">
            <div className="p-5">
              <div className="flex items-center">
                <div className="flex-shrink-0">
                  <div className="w-8 h-8 bg-green-500 rounded-md flex items-center justify-center">
                    <span className="text-white font-semibold text-sm">{stats.availableItems}</span>
                  </div>
                </div>
                <div className="ml-5 w-0 flex-1">
                  <dl>
                    <dt className="text-sm font-medium text-gray-500 truncate">
                      Available
                    </dt>
                    <dd className="text-lg font-medium text-gray-900">
                      {stats.availableItems}
                    </dd>
                  </dl>
                </div>
              </div>
            </div>
          </div>

          <div className="bg-white overflow-hidden shadow rounded-lg">
            <div className="p-5">
              <div className="flex items-center">
                <div className="flex-shrink-0">
                  <div className="w-8 h-8 bg-yellow-500 rounded-md flex items-center justify-center">
                    <span className="text-white font-semibold text-sm">{stats.pendingItems}</span>
                  </div>
                </div>
                <div className="ml-5 w-0 flex-1">
                  <dl>
                    <dt className="text-sm font-medium text-gray-500 truncate">
                      Allocated
                    </dt>
                    <dd className="text-lg font-medium text-gray-900">
                      {stats.pendingItems}
                    </dd>
                  </dl>
                </div>
              </div>
            </div>
          </div>

          <div className="bg-white overflow-hidden shadow rounded-lg">
            <div className="p-5">
              <div className="flex items-center">
                <div className="flex-shrink-0">
                  <div className="w-8 h-8 bg-red-500 rounded-md flex items-center justify-center">
                    <span className="text-white font-semibold text-sm">{stats.soldItems}</span>
                  </div>
                </div>
                <div className="ml-5 w-0 flex-1">
                  <dl>
                    <dt className="text-sm font-medium text-gray-500 truncate">
                      Sold
                    </dt>
                    <dd className="text-lg font-medium text-gray-900">
                      {stats.soldItems}
                    </dd>
                  </dl>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Tabs */}
      <div className="bg-white shadow rounded-lg">
        <div className="border-b border-gray-200">
          <nav className="-mb-px flex space-x-8 px-6">
            {tabs.map((tab) => {
              const Icon = tab.icon
              return (
                <button
                  key={tab.id}
                  onClick={() => setActiveTab(tab.id)}
                  className={`py-4 px-1 border-b-2 font-medium text-base flex items-center ${
                    activeTab === tab.id
                      ? 'border-primary-500 text-primary-600'
                      : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                  }`}
                >
                  <Icon className="h-4 w-4 mr-2" />
                  {tab.name}
                </button>
              )
            })}
          </nav>
        </div>

        {/* Action Buttons - Below tabs, above search */}
        <div className="px-6 py-4 border-b border-gray-200">
          <div className="flex flex-col sm:flex-row sm:justify-between sm:items-center gap-4">
            {activeTab === 'inventory' && (
              <div className="flex items-center gap-4">
                <Link
                  to="/business-inventory/add"
                  className="inline-flex items-center justify-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-amber-600 hover:bg-amber-700 transition-colors duration-200 w-full sm:w-auto"
                >
                  <Plus className="h-4 w-4 mr-2" />
                  Add Item
                </Link>
              </div>
            )}

            {activeTab === 'transactions' && (
              <div className="flex items-center gap-4">
                <Link
                  to="/business-inventory/transaction/add"
                  className="inline-flex items-center justify-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-amber-600 hover:bg-amber-700 transition-colors duration-200 w-full sm:w-auto"
                >
                  <Plus className="h-4 w-4 mr-2" />
                  Add Transaction
                </Link>

                {/* Transaction Status Filter */}
                <select
                  value={transactionStatusFilter}
                  onChange={(e) => setTransactionStatusFilter(e.target.value)}
                  className={`inline-flex items-center px-3 py-2 text-sm font-medium rounded-md transition-colors ${
                    transactionStatusFilter
                      ? 'bg-yellow-100 text-yellow-800 border border-yellow-300'
                      : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                  }`}
                >
                  <option value="">All Status</option>
                  <option value="pending">Pending</option>
                  <option value="completed">Completed</option>
                  <option value="cancelled">Cancelled</option>
                </select>
              </div>
            )}

            {transactionStatusFilter && activeTab === 'transactions' && (
              <div className="text-sm text-gray-600">
                Showing {filteredTransactions.length} {transactionStatusFilter} transaction{filteredTransactions.length !== 1 ? 's' : ''}
              </div>
            )}
          </div>
        </div>

        {/* Tab Content */}
        <div className="px-6 py-6">
          {activeTab === 'inventory' && (
            <>
              {/* Filters and Search - Below action buttons */}
              <div className="mb-6">
                <div className="flex flex-col sm:flex-row gap-4">
                  {/* Search */}
                  <div className="flex-1">
                    <label htmlFor="search" className="sr-only">
                      Search items
                    </label>
                    <div className="relative">
                      <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                        <Search className="h-4 w-4 text-gray-400" />
                      </div>
                      <input
                        type="text"
                        name="search"
                        id="search"
                        className="block w-full pl-10 pr-3 py-2 border border-gray-300 rounded-md leading-5 bg-white placeholder-gray-500 focus:outline-none focus:placeholder-gray-400 focus:ring-1 focus:ring-primary-500 focus:border-primary-500 sm:text-sm"
                        placeholder="Search items by description, SKU, or location..."
                        value={filters.searchQuery || ''}
                        onChange={(e) => handleSearchChange(e.target.value)}
                      />
                    </div>
                  </div>

                  {/* Status Filter */}
                  <div className="sm:w-48">
                    <label htmlFor="status-filter" className="sr-only">
                      Filter by status
                    </label>
                    <select
                      id="status-filter"
                      name="status-filter"
                      className="block w-full pl-3 pr-10 py-2 text-base border-gray-300 focus:outline-none focus:ring-primary-500 focus:border-primary-500 sm:text-sm rounded-md"
                      value={filters.status || ''}
                      onChange={(e) => handleStatusFilterChange(e.target.value)}
                    >
                      <option value="">All Status</option>
                      <option value="available">Available</option>
                      <option value="pending">Allocated</option>
                      <option value="sold">Sold</option>
                    </select>
                  </div>
                </div>
              </div>

              {/* Items List */}
              {items.length === 0 ? (
                <div className="text-center py-12 px-4">
                  <div className="mx-auto h-16 w-16 text-gray-400 -mb-1">üì¶</div>
                  <h3 className="text-lg font-medium text-gray-900 mb-1">
                    No items found
                  </h3>
                  <p className="text-sm text-gray-500 mb-4">
                    {filters.searchQuery || filters.status
                      ? 'Try adjusting your search or filter criteria.'
                      : 'Get started by adding your first business inventory item.'
                    }
                  </p>
                  {!filters.searchQuery && !filters.status && (
                    <Link
                      to="/business-inventory/add"
                      className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-primary-600 hover:bg-primary-700"
                    >
                      <Plus className="h-4 w-4 mr-2" />
                      Add Item
                    </Link>
                  )}
                </div>
              ) : (
                <div className="bg-white shadow overflow-hidden sm:rounded-md">
                  <ul className="divide-y divide-gray-200">
                    {items.map((item) => (
                      <li key={item.item_id} className="relative">
                        <Link
                          to={`/business-inventory/${item.item_id}`}
                          className="block bg-gray-50 transition-colors duration-200 hover:bg-gray-100 active:bg-gray-200"
                        >
                          <div className="px-4 py-4 sm:px-6">
                            {/* Top row: Header with description and status */}
                            <div className="flex items-center justify-between mb-3">
                              <div className="flex items-center">
                                <h3 className="text-base font-medium text-gray-900">
                                  {item.description}
                                </h3>
                                {item.bookmark && (
                                  <span className="ml-2 inline-flex items-center px-2 py-0.5 rounded text-xs font-medium bg-yellow-100 text-yellow-800">
                                    ‚≠ê Bookmarked
                                  </span>
                                )}
                              </div>
                              <div className="flex items-center gap-2">
                                <span className={`inline-flex items-center px-3 py-1 rounded-full text-sm font-medium ${
                                  item.inventory_status === 'available'
                                    ? 'bg-green-100 text-green-800'
                                    : item.inventory_status === 'pending'
                                    ? 'bg-yellow-100 text-yellow-800'
                                    : 'bg-red-100 text-red-800'
                                }`}>
                                  {item.inventory_status === 'available' ? 'Available' :
                                   item.inventory_status === 'pending' ? 'Allocated' : 'Sold'}
                                </span>
                              </div>
                            </div>

                            {/* Bottom row: Details */}
                            <div className="space-y-2">
                              {/* Details row - Price, source, location */}
                              <div className="flex items-center flex-wrap gap-x-4 gap-y-1 text-sm text-gray-500">
                                <span className="font-medium text-gray-700">{formatCurrency(item.price)}</span>
                                <span className="hidden sm:inline">‚Ä¢</span>
                                <span className="font-medium text-gray-700 capitalize">{item.source}</span>
                                {item.business_inventory_location && (
                                  <>
                                    <span className="hidden sm:inline">‚Ä¢</span>
                                    <span className="font-medium text-gray-700">{item.business_inventory_location}</span>
                                  </>
                                )}
                              </div>

                              {/* Project assignment */}
                              {item.current_project_id && (
                                <div className="text-sm text-gray-600">
                                  <span className="font-medium">Allocated to Project:</span> {item.current_project_id}
                                </div>
                              )}

                              {/* Notes */}
                              {item.notes && (
                                <p className="text-sm text-gray-600 line-clamp-2">
                                  {item.notes}
                                </p>
                              )}
                            </div>

                          </div>
                        </Link>
                      </li>
                    ))}
                  </ul>
                </div>
              )}
            </>
          )}

          {activeTab === 'transactions' && (
            <>
              {/* Transactions List */}
              {filteredTransactions.length === 0 ? (
                <div className="text-center py-12 px-4">
                  <div className="mx-auto h-16 w-16 text-gray-400 -mb-1">üßæ</div>
                  <h3 className="text-lg font-medium text-gray-900 mb-1">
                    {transactionStatusFilter ? `No ${transactionStatusFilter} transactions` : 'No inventory transactions yet'}
                  </h3>
                  <p className="text-sm text-gray-500 mb-4">
                    {transactionStatusFilter
                      ? `No transactions found with status "${transactionStatusFilter}".`
                      : 'Inventory-related transactions will appear here when items are allocated to projects or when client-purchased items are moved to business inventory.'
                    }
                  </p>
                </div>
              ) : (
                <div className="bg-white shadow overflow-hidden sm:rounded-md">
                  <ul className="divide-y divide-gray-200">
                    {filteredTransactions.map((transaction) => (
                      <li key={transaction.transaction_id} className="relative">
                        <div className="flex items-center">
                          <Link
                            to={`/project/${transaction.project_id}/transaction/${transaction.transaction_id}`}
                            className="flex-1 block px-4 py-4 sm:px-6 hover:bg-gray-100 transition-colors"
                          >
                            {/* Top row: Header with source and type */}
                            <div className="flex items-center justify-between mb-3">
                              <div className="flex items-center">
                                <h3 className="text-base font-medium text-gray-900">
                                  {transaction.source}
                                </h3>
                              </div>
                              <div className="flex items-center flex-wrap gap-2">
                                {/* Status Badge */}
                                {transaction.status === 'pending' && (
                                  <span className="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium bg-yellow-100 text-yellow-800">
                                    Pending
                                  </span>
                                )}
                                {transaction.status === 'completed' && (
                                  <span className="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium bg-green-100 text-green-800">
                                    Completed
                                  </span>
                                )}
                                {transaction.status === 'cancelled' && (
                                  <span className="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium bg-red-100 text-red-800">
                                    Cancelled
                                  </span>
                                )}

                                {/* Reimbursement Type Badge */}
                                {transaction.reimbursement_type && (
                                  <span className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-medium ${
                                    transaction.reimbursement_type === 'Client owes us'
                                      ? 'bg-blue-100 text-blue-800'
                                      : 'bg-purple-100 text-purple-800'
                                  }`}>
                                    {transaction.reimbursement_type}
                                  </span>
                                )}

                                {transaction.budget_category && (
                                  <span className={`inline-flex items-center px-3 py-1 rounded-full text-sm font-medium ${
                                    transaction.budget_category === 'Design Fee'
                                      ? 'bg-amber-100 text-amber-800'
                                      : transaction.budget_category === 'Furnishings'
                                      ? 'bg-yellow-100 text-yellow-800'
                                      : transaction.budget_category === 'Property Management'
                                      ? 'bg-orange-100 text-orange-800'
                                      : transaction.budget_category === 'Kitchen'
                                      ? 'bg-amber-200 text-amber-900'
                                      : transaction.budget_category === 'Install'
                                      ? 'bg-yellow-200 text-yellow-900'
                                      : transaction.budget_category === 'Storage & Receiving'
                                      ? 'bg-orange-200 text-orange-900'
                                      : transaction.budget_category === 'Fuel'
                                      ? 'bg-amber-300 text-amber-900'
                                      : 'bg-gray-100 text-gray-800'
                                  }`}>
                                    {transaction.budget_category}
                                  </span>
                                )}
                                <span className={`inline-flex items-center px-3 py-1 rounded-full text-sm font-medium no-icon ${
                                  transaction.transaction_type === 'Purchase'
                                    ? 'bg-green-100 text-green-800'
                                    : transaction.transaction_type === 'Return'
                                    ? 'bg-red-100 text-red-800'
                                    : 'bg-gray-100 text-gray-800'
                                }`}>
                                  {transaction.transaction_type}
                                </span>
                              </div>
                            </div>

                            {/* Bottom row: Details */}
                            <div className="space-y-2">
                              {/* Details row - Price, payment method, date */}
                              <div className="flex items-center flex-wrap gap-x-4 gap-y-1 text-sm text-gray-500">
                                <span className="font-medium text-gray-700">{formatCurrency(transaction.amount)}</span>
                                <span className="hidden sm:inline">‚Ä¢</span>
                                <span className="font-medium text-gray-700 capitalize">{transaction.payment_method}</span>
                                <span className="hidden sm:inline">‚Ä¢</span>
                                <span className="font-medium text-gray-700">{formatDate(transaction.transaction_date)}</span>
                              </div>

                              {/* Project name */}
                              <div className="text-sm text-gray-600">
                                <span className="font-medium">Project:</span> {transaction.project_name}
                              </div>

                              {/* Notes */}
                              {transaction.notes && (
                                <p className="text-sm text-gray-600 line-clamp-2">
                                  {transaction.notes}
                                </p>
                              )}
                            </div>
                          </Link>

                          {/* Action buttons */}
                          <div className="px-4 py-4 flex items-center gap-2">
                            <Link
                              to={`/project/${transaction.project_id}/transaction/${transaction.transaction_id}`}
                              className="p-2 text-gray-400 hover:text-gray-600 transition-colors"
                              title="View Transaction"
                            >
                              <Eye className="h-4 w-4" />
                            </Link>
                            <Link
                              to={`/business-inventory/transaction/${transaction.project_id}/${transaction.transaction_id}/edit`}
                              className="p-2 text-gray-400 hover:text-gray-600 transition-colors"
                              title="Edit Transaction"
                            >
                              <Edit className="h-4 w-4" />
                            </Link>
                          </div>
                        </div>
                      </li>
                    ))}
                  </ul>
                </div>
              )}
            </>
          )}
        </div>
      </div>
    </div>
  )
}
